/* CREDIT: Code taken from Code Institute's "Moments" walkthrough project */
.App {
  text-align: center;
  background-color: #d8d3cd;
  min-height: 100vh;
}

* {
  box-sizing: border-box;
}

svg {
  color: #df604e;
  transition: color ease-in-out 0.3s;
}

svg:hover {
  color: #af3c2d;
  transition: color ease-in-out 0.3s;
}

.Content {
  background-color: #ffffff;
  padding: 10px;
  border-radius: 5px;
  border: 3px solid #5a5550;
}

.FillerImage {
  object-fit: cover;
  height: 100%;
  width: 100%;
}

.PaddingReset {
  padding: 0;
}

/* scrollbar */
*::-webkit-scrollbar {
  width: 5px;
}
*::-webkit-scrollbar-track {
  background: #f1f1f1;
}
*::-webkit-scrollbar-thumb {
  background: #888;
}
*::-webkit-scrollbar-thumb:hover {
  background: #555;
}
